import React from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { getLocalAuthRedirectURL } from '@dnb/authentication';
import { bell_medium, log_in_medium, loupe_medium } from '@dnb/eufemia/icons';
import { navigate } from 'gatsby';
import MenuButton from '../MenuButton/MenuButton';
import ProfileDropdown from '../ProfileDropdown/ProfileDropdown';
import styles from './HeaderRight.module.scss';

type HeaderItemsProps = { isLoggedIn?: boolean; shouldOpen?: boolean };

// TODO: Replace this with real implementation when notifications service is ready
const getNotificationsNumber = () => 6;

const AuthenticatedHeaderItems = ({ shouldOpen }: HeaderItemsProps) => {
  const intl = useIntl();
  const notifications = getNotificationsNumber();
  const notificationsButtonAriaLabel = intl.formatMessage(
    { id: 'dnb_header_activity_notifications_alt_text' },
    { numNotifications: notifications }
  );

  return (
    <>
      <MenuButton
        text={<FormattedMessage id="dnb_header_search_button" />}
        icon={loupe_medium}
        on_click={() => navigate('/')}
        disabled // TODO: Enable once search is implemented
      />

      <MenuButton
        text={<FormattedMessage id="dnb_header_activity_button" />}
        icon={bell_medium}
        aria-label={notificationsButtonAriaLabel}
        badge={notifications}
        on_click={() => navigate('/')}
        disabled // TODO: Enable once notifications and authentication are implemented
      />

      <ProfileDropdown shouldOpen={shouldOpen} />
    </>
  );
};

const DefaultHeaderItems = () => {
  return (
    <>
      <MenuButton
        text={<FormattedMessage id="dnb_header_search_button" />}
        icon={loupe_medium}
        on_click={() => navigate('/')}
        disabled // TODO: Enable once search is implemented
      />

      <MenuButton
        text={<FormattedMessage id="dnb_header_login_button" />}
        icon={log_in_medium}
        on_click={() => navigate(getLocalAuthRedirectURL('/'))}
      />
    </>
  );
};

const HeaderRight = ({ isLoggedIn, shouldOpen }: HeaderItemsProps) => {
  return (
    <div className={styles.dnbHeaderRight}>
      {isLoggedIn ? (
        <AuthenticatedHeaderItems shouldOpen={shouldOpen} />
      ) : (
        <DefaultHeaderItems />
      )}
    </div>
  );
};

export default HeaderRight;
